import React from "react";
import {Route, Switch} from "react-router-dom";

import './App.css';

import HomePage from "./components/home/HomePage.js";
import Project1 from "./components/project1/Project1";


const App = () => {

    return(
        <>
        <Switch>
          <Route path = "/react-gh-pages/" exact component = {HomePage}/>
          <Route path = "/react-gh-pages/project1" exact component = {Project1}/>

        </Switch>
        
        </>
    )

}

export default App;
